@import url('https://fonts.googleapis.com/css?family=Roboto:300,400,700|Roboto+Mono|Poppins|Work+Sans');

:root {
  /* #3274B7 */
  --pandera-blue: rgb(50, 116, 183);
  --dark-gray: #374047;
  --gray-text: #4a4a4a;
  --light-gray: #d4d4d4;
  --main-box-shadow: rgba(0, 0, 0, 0.19) 0 10px 20px, rgba(0, 0, 0, 0.23) 0 6px 6px;
  --slide-height: 1080px;
  --slide-header-height: 180px;
  --slide-footer-height: 75px;
}

body {
  height: 100vh;
  margin: 0;
  padding: 0;
}

nav > *, button, h1, h2, h3, h4, h5, aside > * {
  font-family: "Work Sans", Helvetica, sans-serif;
}

.react-monaco-editor-container {
  width: auto !important;
}

.monaco-editor {
  max-height: 928px;
}

#contents {
  -webkit-box-shadow: var(--main-box-shadow);
          box-shadow: var(--main-box-shadow);
}

.renderErrorTitle {
  font-size: 24px;
  font-weight: bold;
}

.renderErrorStack {
  font-size: 12px;
  white-space: pre-wrap;
  max-height: 600px;
}

.renderErrorHeader {
  font-size: 18px;
  font-weight: bold;
  margin: 8px 0;
}

.slideText {
  alignment-baseline: central;
  fill: var(--gray-text);
}

.slideHeader {
  height: var(--slide-header-height);
}

.slideHeader > rect {
  width: 100%;
  height: var(--slide-header-height);
  fill: var(--light-gray)
}

.slideHeader > text {
  font-family: "Proxima Nova Light", sans-serif;
  font-size: 72px;
  font-weight: 100;
  text-anchor: middle;
}

.slideFooter > line {
  stroke: var(--light-gray);
  stroke-width: 2px;
}

.slideFooter > text {
  font-size: 24px;
  text-anchor: middle;
}

.bulletedItemText {
  font-family: "Work Sans", sans-serif;
  font-size: 48px;
}

.filledBullet {
  fill: var(--dark-gray);
}

.hollowBullet {
  fill: none;
  stroke: var(--dark-gray);
  stroke-width: 2px;
}

.slideFooter > line {
  stroke: var(--light-gray);
  stroke-width: 2px;
}

.slideFooter > text {
  font-size: 24px;
  text-anchor: middle;
}

.editorGlyphMargin {
  content: url(data:image/svg+xml,%3Csvg%20xmlns%3D%22http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg%22%20width%3D%2248%22%20height%3D%2248%22%20viewBox%3D%220%200%2048%2048%22%3E%3Cpath%20fill%3D%22%230E7E12%22%20d%3D%22M34%206H14c-2.21%200-3.98%201.79-3.98%204L10%2042l14-6%2014%206V10c0-2.21-1.79-4-4-4zm0%2030l-10-4.35L14%2036V10h20v26z%22%2F%3E%3C%2Fsvg%3E);
}

.editorLineHighlight {
  background: rgba(14, 126, 18, .25);
}




